@import '../index.scss';
@import './work.scss';
@import './exhibition-form.scss';
@import './exhibition_modal.scss';

.works-container {
    max-width: 100%;
    height: 100vh;
    overflow-y: scroll;
    padding: 2rem 1rem 1rem 1rem;
    box-sizing: border-box;

        &::-webkit-scrollbar { 
            display: none;
         }
    
     .works-title{
        @include font($frank-ruhl-libre, normal, $l);
        color: $dark_magenta;
        letter-spacing: 0.3rem;     
        padding-left: 1rem;
    }

    .works-wrapper {
        @include flexbox-direction(flex, flex-start, column, space-between);
        gap: 3rem;
        margin-top: 2.5rem;
        border-radius: 50px;
        &::-webkit-scrollbar {
            display: none;
        }
      
        .exhibition-wrapper {
            @include flexbox(flex, flex-start, space-between);
            gap: 2rem;
            height: 200%;
            min-width: 100%;
            overflow-x:auto;   
            overflow-y: hidden;
            margin: 0 auto;

             &::-webkit-scrollbar { 
               display: none;
            }

            @include monitor {
                height: 200%;
            }
        }

        .scroll_right {
            position: absolute;
            top: 50%;
            right: 5%;
            transform: translateY(-50%);
            padding: 10px;
            background-color: #f1f1f1;
            border: none;
            outline: none;
            // display: none;

            &::-webkit-scrollbar {
                display: none;
            }
        }
    }
}
   